import React from 'react';
import './Day.css';
export const Day = (props) =>{

    const styles = {
        
        backgroundImage:`url(${props.Cover})`,
      }
    return(
        
         <div id={props.iD}onClick={props.onClickDay}role="img" aria-label={`${props.name}`} className=" link mw5 dt hide-child br2 ba b--gold bw cover bg-center bg-animate hover-bg-light-gray grow br2 ma1 card tc " style={styles} >
            <span className=" white dtc v-mid w-100 h-100 child bg-black-40 pa5  bg-animate hover-bg-blue ">
                <p>
                    {`Day ${props.iD}`}
                </p>
            </span>
        </div> 
        
        
        
    )
};


